@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500;600;700;800;900&display=swap');

@layer base {
	body{
		@apply font-[Raleway];
	}
	li {
		@apply px-4;
		@apply cursor-pointer
	}
}

body{
	background-color: #0e1111d3;
}

/* ------------------------ */

.full-height {
	height: 100vh;
}

.hidden {
	display: none;
}

/* ------------ Active link change style on n ------------ */
.activeLinkInNavbar{
	color: #07ed66;
	text-decoration: underline;
}

/* ------------ Hide on desktop or mobile mode  ------------*/
@media screen and (max-width: 1023px){
	.hideOnMobileMode{
		display: none;
	}
}
@media screen and (min-width: 1023px){
	.hideOnDesktopMode{
		display: none;
	}
}
/* ------------------------------------ */
/* (Card style was to long on tailwind) */
.card {
	box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
	background: rgba(255, 255, 255, 0.1);
	overflow: hidden;
	border-top: 1px solid rgba(255, 255, 255, 0.199);
	border-left: 1px solid rgba(255, 255, 255, 0.199);
	backdrop-filter: blur(5px);
}